.profile {
  width: 250px;
  text-align: center;
  margin: auto;
  border: solid 1px #d8e0ea;
}

.profile .name {
  font-size: 18px;
  color: #333333;
  font-weight: 700;
  margin-top: 30px;
}

.profile .tag {
  font-size: 14px;
  color: #666666;
  text-align: center;
  margin-top: 10px;
}

.profile .stats {
  display: flex;
  justify-content: space-evenly;
  list-style: none;
  background-color: #f3f6f9;
  border-radius: 0 0 5px 5px;
}

.profile .label {
  color: #97a2ae;
  padding-top: 22px;
  font-size: 12px;
  display: flex;
}

.profile .quantity {
  padding-bottom: 22px;
  line-height: 1.2;
  font-weight: bold;
  font-size: 14px;
  display: flex;
}

.profile li {
  flex-direction: column;
  align-items: center;
  border: solid 1px #d8e0ea;
  width: 85px;
}